import React from 'react'; // Import the Component component from React


 
function Fold(props) {
  return (
    
        <div className="fold" data-expand={props.visibility} >
            <div className ="fold-head col-lg-12">
                <h5 className="float-left "> {props.header}</h5>
                <span className ="float-right"><i class="arrow down">  </i> </span>
            </div>
            
            <div className ="fold-content col-lg-12 pt-3"> {props.children}
            </div>

        </div>
        

 
  );
}
 
export default Fold;