import React from 'react'; // Import the Component component from React


 
function Hero(props) {
  return (
<div className="row section-lg no-gutters ">
            <div className= {'col-lg hero-img colorthis order-last '} > <img src= {props.img}  alt="Portfolio "></img> </div>
            <div className= {'col-lg hero-content d-flex align-items-center  justify-content-center'} > 
            
            {props.children}
            
            </div>
         
          </div>
  );
}
 
export default Hero;