import React from 'react'; // Import the Component component from React




function Section(props) {


  return (
<div className="row section-lg min-h-5  section" >
            <div className= {'col-lg-4 content  section-title d-flex align-items-center  justify-content-center' + (props.direction==="left"? ' order-last': ' order-first') } style ={{ backgroundColor: props.pcolor }} ><h1 className="display-3"> {props.title}</h1> </div>
            <div className= {'col-lg-8 content section-content py-5 px-3  justify-content-center '}  style ={{ backgroundColor: props.scolor }}> {props.children} </div>
          </div>
  );
}
 
export default Section;